import React, { Fragment } from "react"

import style from "./TableTitle.module.css"

type ITableTitle = {
  count: number
}

const TableTitle: React.FC<ITableTitle> = ({ count }) => {
  return (
    <Fragment>
      {count !== 0 ? (
        <div className={style.searchTableTitles}>
          <span className={style.imageContainer}></span>
          <span className={style.fullName}>  | Username</span>
          <span className={style.city}> | Loaction</span>
          <span className={style.instrumentName}>
              | Main Instrument
          </span>
          <span className={style.level}> | Level</span>
          <span className={style.style}> | Style</span>
        </div>
      ) : (
        <div className={style.noResult}>   </div>
      )}
    </Fragment>
  )
}

export { TableTitle }
