import axios from "axios"
import Cookie from "js-cookie"

axios.interceptors.request.use(
  (response) => {
    response.headers.common["Authorization"] = Cookie.get("accesstoken") + ""
    return response
  },
  function (error) {}
)

axios.interceptors.response.use(
  (response) => {
    return response
  },
  function (error) {
    const originalRequest = error.config

    if (
      error.response.status === 401 &&
      originalRequest.url ===
        "https://hamnavazi-api.azurewebsites.net/api/Account/RefreshToken"
    ) {
      window.location.href = "/Auth/Login"
      return Promise.reject(error)
    }

    if (error.response.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true
      // const refreshToken = Cookie.get("refreshtoken")
      const refreshToken =
        "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..SVhosgAjsA3Iql7qjWUFTJ0s30pmVd3RrqikddIh004"
      axios.defaults.headers.common["refreshToken"] = refreshToken + ""

      return axios
        .post(
          "https://hamnavazi-api.azurewebsites.net/api/Account/RefreshToken"
        )
        .then((res) => {
          if (res.status === 200) {
            Cookie.set("accesstoken", res.headers.accesstoken)
            error.config.headers.Authorization = res.headers.accesstoken
            return axios(originalRequest)
          }
        })
    }
    return Promise.reject(error)
  }
)

export default {
  get: axios.get,
  post: axios.post,
  delete: axios.delete,
  put: axios.put,
}
